.seriesContents {
	display: flex;
	visibility: hidden;
	position: absolute;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	transition: visibility 0.5s linear, opacity 0.5s linear;
	opacity: 0;
}

.seriesContents_visible {
	visibility: visible;
	opacity: 1;
}

.seriesContents__container {
	width: 200px;
	height: -webkit-min-content;
	height: min-content;
	position: relative;
	right: -100px;
	top: 80px;
	display: flex;
	flex-direction: column;
	background-color: #ffffff;
	border: 2px solid #000000;
	padding: 0 10px 10px 10px;
	box-sizing: border-box;
}

