.header {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
	padding: 0 10px;
	box-sizing: border-box;
	background-color: #ffffff;
}

.header__content-container {
	width: 100%;
	height: 60px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.header__title {
	margin: 0;
	font-family: 'Quant Antiqua';
	font-style: normal;
	font-weight: 400;
	font-size: 16px;
	line-height: 120%;
	color: var(--simple-text-color);
	text-decoration: none;
}

.header__menu {
	display: grid;
	grid-template-rows: 1fr 1fr;
	grid-row-gap: 10px;
	row-gap: 10px;
}

.header__link {
	margin: 0;
	font-family: 'Quant Antiqua';
	font-style: normal;
	font-weight: 400;
	font-size: 10px;
	line-height: 130%;
	letter-spacing: 0.2em;
	text-transform: uppercase;
	color: var(--simple-text-color);
	text-decoration: none;
}

.header__link:hover {
	cursor: pointer;
}

.header__separator {
	width: 100%;
	height: 1px;
	background-color: #000000;
}

@media screen and (min-width: 425px) {
	.header {
		padding: 0 20px;
	}

	.header__title {
		font-size: 20px;
	}

	.header__link {
		font-size: 12px;
	}
}

@media screen and (min-width: 768px) {
	.header {
		padding: 0 50px;
	}

	.header__title {
		font-size: 28px;
	}

	.header__menu {
		padding-top: 10px;
		box-sizing: border-box;
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-column-gap: 15px;
		-webkit-column-gap: 15px;
		        column-gap: 15px;
		grid-template-rows: none;
	}

	.header__link {
		font-size: 14px;
	}
}

@media screen and (min-width: 1024px) {
