.timelinePart {
	width: 100%;
	display: grid;
	grid-template-columns: 7fr 5px 12fr;
	grid-column-gap: 10px;
	-webkit-column-gap: 10px;
	        column-gap: 10px;
}

.timelinePart__date-container {
	width: 100%;
