.video-popup {
	display: flex;
	visibility: hidden;
	justify-content: center;
	align-items: center;
	position: fixed;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	background-color: rgba(0, 0, 0, 0.5);
	transition: visibility 0.5s linear, opacity 0.5s linear;
	opacity: 0;
}

.video-popup_visible {
	visibility: visible;
	opacity: 1;
}

.video-popup__container {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 80%;
	height: 30%;
	background-color: #ffffff;
	border-radius: 3px;
	padding: 5px;
	box-sizing: border-box;
	position: absolute;
}

.video-popup_close-button {
	width: 26px;
	height: 26px;
	background-image: url(/AU_Memo/static/media/cross.c280e852.svg);
	background-size: contain;
	background-position: center;
	-webkit-transform: rotate(-45deg);
	        transform: rotate(-45deg);
	position: absolute;
	top: -30px;
	right: -30px;
	padding: 0;
	border: 0;
	background-color: rgba(0, 0, 0, 0);
	outline: 0;
}

.video-popup_close-button:hover {
	cursor: pointer;
}

.video-popup__video {
	width: 100%;
	height: 100%;
}

@media screen and (min-width: 768px) {
	.video-popup__container {
		height: 60%;
	}
}

@media screen and (min-width: 1024px) {
	.video-popup__container {
		height: 80%;
	}
}

